import React, { useState } from 'react';
import Navbar from './components/Navbar';
import Hero from './components/Hero';
import Footer from './components/Footer';
import SobreNosotros from './components/SobreNosotros';
import Services from './components/Services';
import Team from './components/Team';
import Gallery from './components/Gallery';
import Testimonials from './components/Testimonials';
import Contact from './components/Contact';

function App() {
  const [activeComponent, setActiveComponent] = useState('inicio');

  const getTitle = () => {
    switch (activeComponent) {
      case 'inicio':
        return 'Inicio';
      case 'aboutus':
        return 'Sobre Nosotros';
      case 'services':
        return 'Servicios';
      case 'team':
        return 'Nuestro Equipo';
      case 'gallery':
        return 'Galería';
      case 'testimonials':
        return 'Testimonios';
      case 'contact':
        return 'Contacto';
      default:
        return 'Inicio';
    }
  };

  const renderComponent = () => {
    switch (activeComponent) {
      case 'inicio':
        return <Hero title={getTitle()} />;
      case 'aboutus':
        return <SobreNosotros title={getTitle()} />;
      case 'services':
        return <Services title={getTitle()} />;
      case 'team':
        return <Team title={getTitle()} />;
      case 'gallery':
        return <Gallery title={getTitle()} />;
      case 'testimonials':
        return <Testimonials title={getTitle()} />;
      case 'contact':
        return <Contact title={getTitle()} />;
      default:
        return <Hero title={getTitle()} />;
    }
  };

  return (
    <div className="min-h-screen">
      <Navbar onMenuClick={setActiveComponent} />
      {renderComponent()}
      <Footer />
    </div>
  );
}

export default App;
